import Vue from 'vue'
import App from '@/App'
import Router from 'vue-router'

import 'html5-reset/assets/css/reset.css'
import activityIndex from '@/view/activity/activity-index.vue';  //活动首页

Vue.config.productionTip = false
Vue.use(Router)

const router = new Router({
  // base: '/activity-index.html',
  routes: [{
    path: '/',
    name: 'activityIndex',
    component: activityIndex
  },]
})

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  components: {
    App
  },
  template: '<App/>'
})



